import {
  useLocation,
  useNavigate,
  useParams,
  useSearchParams,
} from "react-router-dom";
import { useLmsStaffLeaveOverviewCount } from "../../../Hooks/ApiHooks/LmsManager/lms-manager-api";
import { memo, useEffect, useState } from "react";
import { leaveTypeList } from "../../../Constant/Lms/lms_constant";
import TileWrapper from "../../../Ui/Tab/Tile/TileWrapper";
import TileLeaveCard from "../../../Ui/Tab/Tile/TileLeaveCard";
import { useTranslation } from "react-i18next";

const LmsStaffOverviewType = ({ leaveTypeToggle }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const params = useParams();
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();
  const [stateType, setStateType] = useState(leaveTypeList);
  const { lmsStaffLeaveOverviewCount } = useLmsStaffLeaveOverviewCount({
    data: {
      sid: getQuery.state?.staffId,
    },
    skip: !getQuery.state?.staffId,
  });

  useEffect(() => {
    if (lmsStaffLeaveOverviewCount?.staff) {
      let count = lmsStaffLeaveOverviewCount?.staff;
      let lm = leaveTypeList;
      lm[1].count = count?.casual_leave;
      lm[2].count = count?.medical_leave;
      lm[3].count = count?.sick_leave;
      lm[4].count = count?.c_off_leave;
      lm[5].count = count?.off_duty_leave;
      lm[6].count = count?.lwp_leave;
      lm[7].count = count?.commuted_leave;
      lm[8].count = count?.maternity_leave;
      lm[9].count = count?.paternity_leave;
      lm[10].count = count?.quarantine_leave;
      lm[11].count = count?.half_pay_leave;
      lm[12].count = count?.study_leave;
      lm[13].count = count?.sabbatical_leave;
      lm[14].count = count?.special_disability_leave;
      lm[15].count = count?.winter_vacation_leave;
      lm[16].count = count?.summer_vacation_leave;
      lm[17].count = count?.child_adoption_leave;
      lm[18].count = count?.bereavement_leave;
      // lm[19].count = count?.earned_leave;
      setStateType(lm);
    }
  }, [lmsStaffLeaveOverviewCount?.staff]);

  const onSelectType = (val) => {
    let url =
      getQuery.state?.urlOffSet === "Institute"
        ? `/q/${params.username}/institute/member/lms/?type=staff-overview&category=${val}`
        : getQuery.state?.urlOffSet === "Staff"
        ? `/q/${params.username}/member?a=leave&n=overview&category=${val}`
        : `/q/${params.username}/member/staff/lms/?type=staff-overview&category=${val}`;
    navigate(url, {
      state: getQuery.state,
    });
  };

  // console.info("getQuery.state?.urlOffSet", getQuery.state?.urlOffSet);

  return (
    <>
      <TileWrapper
        customTileContainer={{
          flexWrap: "wrap",
          marginBottom: "1rem",
        }}
      >
        {stateType?.map((tile) =>
          tile.key === "All Leave" ? (
            <TileLeaveCard
              tile={tile}
              onTile={onSelectType}
              customTileChild={{
                height: "2.5rem",
              }}
              activeTile={searchParams.get("category")}
            >
              <h6>{t(tile?.name)}</h6>
            </TileLeaveCard>
          ) : (
            leaveTypeToggle[tile?.isActive] && (
              <TileLeaveCard
                tile={tile}
                onTile={onSelectType}
                customTileChild={
                  tile.key === "All Leave"
                    ? {
                        height: "2.5rem",
                      }
                    : {
                        flexDirection:
                          tile.key === "All Leave" ? "row" : "column",
                        alignItems: "flex-start",
                      }
                }
                activeTile={searchParams.get("category")}
              >
                <h6>{t(tile?.name)}</h6>
                {tile?.count >= 0 ? (
                  <p>
                    {t("available")} {": "} {tile?.count}{" "}
                  </p>
                ) : null}
              </TileLeaveCard>
            )
          )
        )}
      </TileWrapper>
    </>
  );
};

export default memo(LmsStaffOverviewType);
