import { useEffect, useState } from "react";
import { useInView } from "react-intersection-observer";
import { useLocation, useSearchParams } from "react-router-dom";
import { nextPage } from "../../../Utils/Functions/nextPage";
import { useLmsStaffLeaveOverviewList } from "../../../Hooks/ApiHooks/LmsManager/lms-manager-api";
import Loading from "../../../Loader/Loading";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import LmsStaffOverviewListCard from "./LmsStaffOverviewListCard";

const LmsStaffOverviewList = () => {
  const getQuery = useLocation();
  const [searchParams] = useSearchParams();
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView();

  const { lmsStaffLeaveOverviewList, lmsStaffLeaveOverviewListLoading } =
    useLmsStaffLeaveOverviewList({
      data: {
        sid: getQuery.state?.staffId,
        page: page,
        limit: 10,
        category:
          searchParams.get("category") === "All Leave"
            ? ""
            : searchParams.get("category"),
      },
      skip: !getQuery.state?.staffId,
    });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (lmsStaffLeaveOverviewList?.list?.length) {
      let obj = nextPage(lmsStaffLeaveOverviewList?.list?.length);
      setState(() => obj);
    }
  }, [lmsStaffLeaveOverviewList?.list]);

  useEffect(() => {
    if (searchParams.get("category")) {
      setPage((prev) => (prev = 1));
    }
  }, [searchParams.get("category")]);
  return (
    <>
      {lmsStaffLeaveOverviewList?.list?.map((leave, index) =>
        lmsStaffLeaveOverviewList?.list?.length === index + 1 ? (
          <div key={leave?._id} ref={ref}>
            <LmsStaffOverviewListCard leave={leave} />
          </div>
        ) : (
          <LmsStaffOverviewListCard leave={leave} key={leave?._id} />
        )
      )}
      {lmsStaffLeaveOverviewListLoading ? (
        <Loading />
      ) : (
        lmsStaffLeaveOverviewList?.list?.length === 0 && (
          <EmptyMessage title={`No ${searchParams.get("category")} taken.`} />
        )
      )}
    </>
  );
};

export default LmsStaffOverviewList;
