import { useTranslation } from "react-i18next";
import { useLocation, useNavigate, useParams } from "react-router-dom";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import style from "../../FinanceManager/FinanceManager.module.css";
import { useLmsStaffLeaveOverviewCount } from "../../../Hooks/ApiHooks/LmsManager/lms-manager-api";
import LmsStaffOverviewType from "./LmsStaffOverviewType";
import LmsStaffOverviewList from "./LmsStaffOverviewList";
import { useCallback } from "react";
import { assestsUtilityUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";

const LmsStaffOverview = ({ isStaff, leaveTypeToggle }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const params = useParams();
  const navigate = useNavigate();
  const { lmsStaffLeaveOverviewCount } = useLmsStaffLeaveOverviewCount({
    data: {
      sid: getQuery.state?.staffId,
    },
    skip: !getQuery.state?.staffId,
  });

  const onNavigate = useCallback(() => {
    let url =
      getQuery.state?.urlOffSet === "Institute"
        ? `/q/${params.username}/institute/member/lms`
        : `/q/${params.username}/member/staff/lms`;
    navigate(url, {
      state: getQuery.state,
    });
  }, [navigate, getQuery.state, params.username]);

  return (
    <>
      {isStaff ? (
        <>
          <UserMemberContentWrapper>
            <LmsStaffOverviewType leaveTypeToggle={leaveTypeToggle} />
            <LmsStaffOverviewList />
          </UserMemberContentWrapper>
        </>
      ) : (
        <UserMemberContainerWrapper>
          <UserMemberContentWrapper>
            <div className={style.fm_header_container}>
              <div className={style.fm_header_start_container}>
                <img
                  alt="search icon"
                  src={`${assestsUtilityUrl}/back.svg`}
                  style={{
                    height: "2rem",
                    width: "2rem",
                  }}
                  onClick={onNavigate}
                />
                <h6>
                  <span
                    style={{
                      fontWeight: "400",
                    }}
                  >
                    {t("name")}
                  </span>

                  {": "}
                  {getQuery?.state?.staffName ?? ""}
                </h6>
              </div>

              <div
                className={style.fm_header_container}
                style={{
                  gap: "1rem",
                }}
              >
                <h6>
                  <span
                    style={{
                      fontWeight: "400",
                    }}
                  >
                    {t("total_leave_available")}
                  </span>
                  {": "}
                  {lmsStaffLeaveOverviewCount?.overview
                    ?.total_leave_available ?? 0}
                </h6>
                <h6>
                  <span
                    style={{
                      fontWeight: "400",
                    }}
                  >
                    {t("total_leave_taken")}
                  </span>
                  {": "}
                  {lmsStaffLeaveOverviewCount?.overview?.total_leave_taken ?? 0}
                </h6>
              </div>
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper>
            <LmsStaffOverviewType leaveTypeToggle={leaveTypeToggle} />
            <LmsStaffOverviewList />
          </UserMemberContentWrapper>
        </UserMemberContainerWrapper>
      )}
    </>
  );
};

export default LmsStaffOverview;
